header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

.name {
  color: var(--color-primary);
  font-family: Dancing Script;
}

/* ================== CTA ================== */
.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* ================== HEADER SOCIAL ICONS ================== */
.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header__socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--color-white);
}

/* ================== HEADER IMAGE ================== */
.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 20rem;
  height: 24rem;
  position: absolute;
  left: calc(50% - 10rem);
  margin-top: 2rem;
  border-radius: 12rem 12rem 0 0; /*tl tr br bl */
  overflow: hidden;
  padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/* ================== SCROLL DOWN & DOWN ICON ================== */
.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 7rem;
  transform: rotate(90deg);
  font-weight: 500;
  font-size: 0.9rem;
}

.down-icon {
  transform: rotate(360deg);
  color: var(--color-white);
  bottom: 3rem;
  right: -0.2rem;
}

/* ================== MEDIA QUERIES(Medium Devies) ================== */
@media screen and (max-width: 1024px) {
  header {
    height: 80vh;
  }
}

/* ================== MEDIA QUERIES(Small Devies) ================== */
@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }
  .header__socials,
  .scroll__down,
  .down-icon {
    display: none;
  }
}
