.education__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
}

.education__container > div {
  background-color: var(--color-bg-variant);
  padding: 1.4rem 4rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
}

.education__container > div:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}

.education__container > div > h3 {
  text-align: center;
  color: var(--color-primary);
}

.my__education {
  display: flex;
  gap: 1rem;
}

.course__name {
  color: var(--color-primary);
}

.brief__education {
  margin-bottom: 2rem;
  text-align: justify;
}

.education__details {
  display: flex;
  gap: 1rem;
}

.education__icon {
  margin-top: 6px;
}

/* ================== MEDIA QUERIES(Medium Devies) ================== */
@media screen and (max-width: 1024px) {
  .education__container {
    grid-template-columns: 1fr;
  }

  .education__container > div {
    width: 80%;
    padding: 2rem;
    margin: 0 auto;
  }

  .education__content {
    padding: 1rem;
  }
}

/* ================== MEDIA QUERIES(Small  Devies) ================== */
@media screen and (max-width: 600px) {
  .education__container {
    gap: 1rem;
  }

  .education__container > div {
    width: 100%;
    padding: 2rem 1rem;
  }
}
